import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import successIcon from "../../assets/images/icons/success.svg";
import errorIcon from "../../assets/images/icons/error.svg";
import { useTranslation } from "react-i18next";


const MessageModal = ({ show, handleShow, formStatus }) => {
  const { t } = useTranslation();
  return (
    <Modal show={show} onHide={handleShow} centered className="info-modal">
      <Modal.Header closeButton></Modal.Header>
      <Modal.Body>
        <img src={formStatus === "success" ? successIcon : errorIcon } alt="icon" />
        <h6>{formStatus === "success" ? t("info.success") : t("info.error")}</h6>
      </Modal.Body>
      <Modal.Footer>
        <Button className="secondary-btn" onClick={handleShow}>
          {t("info.close")}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default MessageModal;
