import { useEffect, useState } from "react";
import axios from "axios";
import { useTranslation } from "react-i18next";
import avtar from "../../assets/images/icons/step1_icon.png";
import { Link } from "react-router-dom";
import { serverPath } from "../../config/key";

const OurExpertOld = () => {
  const { t } = useTranslation();
  const [currentPhysiciansList, setCurrentPhysiciansList] = useState([]);
  const getAllPhysicians = async () => {
    try {
      const response = await axios.get(`${serverPath}/public/doctors`);
      const { status, data } = response || {};
      if (status === 200) {
        handlePhysicianShuffle(data);
      }
    } catch (error) {
      console.log("error in Physicians api ==>", error);
    }
  };
  const chooseRandom = (arr, num = 1) => {
    const res = [];
    for (let i = 0; i < num; ) {
      const random = Math.floor(Math.random() * arr.length);
      if (res.indexOf(arr[random]) !== -1) {
        continue;
      }
      res.push(arr[random]);
      i++;
    }
    return res;
  };

  const handlePhysicianShuffle = (allDoctors) => {
    const interval = setInterval(() => {
      let unshuffled = chooseRandom(allDoctors, 12);
      setCurrentPhysiciansList(unshuffled);
    }, 3000);
    return () => {
      clearInterval(interval);
    };
  };

  useEffect(() => {
    getAllPhysicians();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div className="our-expert" id="experts-section">
      <div className="container">
        <div className="our-expert-title">
          <h2> {t("ourExpert.heading")}</h2>
          <p>{t("ourExpert.para1")}</p>
          <p>{t("ourExpert.para2")}</p>
        </div>
        <div className="our-expert-grid">
          {currentPhysiciansList.map((el, i) => (
            <div className="our-expert-box" key={i}>
              <div className="our-expert-profile">
                <img
                  src={el.profilePictureUrl ? el.profilePictureUrl : avtar}
                  alt="avtar"
                  className={`${!el.profilePictureUrl ? "no-profile" : ""}`}
                />
              </div>

              <div className="our-expert-content">
                <h6>{el.name}</h6>
                <p>{el.category}</p>
              </div>
            </div>
          ))}
        </div>
        <div className="appointment-btn">
          <Link
            to="https://my.hidoc.co.il/meetingrequest"
            className="secondary-btn"
          >
            {t("homeBanner.button")}
          </Link>
        </div>
      </div>
    </div>
  );
};

export default OurExpertOld;
